body {
  font-family: "Do Hyeon", sans-serif;
  margin: 0px;
  font-size: calc(10px + 2vmin); }

h1 {
  font-size: 3rem; }

h2 {
  font-size: 2rem; }

a {
  color: #ffffff;
  text-decoration: none;
  background-color: #5b6578;
  padding: 0.8em 2em;
  border-radius: 5px; }
  a:hover {
    opacity: 0.5; }

button {
  color: #ffffff;
  padding: 2em;
  margin: 1em;
  background-color: #5b6578;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer; }

select {
  background-color: transparent;
  border: #ffffff solid 2px;
  color: #ffffff;
  width: 100%;
  height: 40px;
  font-size: 1.2rem; }

span {
  font-size: 1.8rem; }
  span.caption {
    color: red; }

.green {
  color: green; }

.red {
  color: red; }

.App {
  text-align: center;
  padding: 2em;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

